	module.exports = {"data":{"contentfulTheorie":{"titre":"New Synthetic Humans","texte":{"childMarkdownRemark":{"html":"<p>New Synthetic Humans are individuals who have obtained powers via a serum that allows them to pass from inactive genes to fictitious genes, similar to active genes. It is possible that the powers take time to manifest themselves or that the body rejects the serum in question, causing discomfort and some physical suffering. But if the serum works properly after one or more doses, the powers remain permanently present. However, the gene will not be transmitted by reproduction.</p>\n<p>Darakei Drumel is the individual who created this serum for research purposes. Julius Anderson is the man responsible for the theft of the serum and the activation of powers on a dozen former military personnel. (See chapter <a href=\"/en/stories/chapter/chapter-11-the-drumel-genes\">11</a> and <a href=\"/en/stories/chapter/chapter-12-theft\">12</a> of the novel <a href=\"/en/stories/the-first-cyborg-volume-2\">The First Cyborg - Volume 2</a>)</p>"}},"equivalentUrl":"/encyclopedie/nouveaux-humains-de-synthese"}},"pathContext":{"slug":"new-synthetic-humans"}}

